.accordion__itemIconWrap {
    width: 20px;
    height:20px;
    margin-left: auto;
    position: absolute;
    z-index: 10;
    top: 10px;
    right: 0;
    color: black;
}
.accordion__itemIconWrap svg {
    filter: brightness(0);
}
.accordion-header::after {
    content: '';
    position: absolute;
    top: auto;
    left: 0;
    width: 100%;
    height: 1px;
    z-index: 1;
    transition: transform .4s ease;
    background: silver;
    bottom: -10px;
}
.accordion__item.is-active .accordion__itemTitleWrap::after {
    -webkit-transform: translateX(-20%);
    transform: translateX(-20%);
}
h2.accordion-header {
    position: relative;
    margin-bottom: 15px;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    letter-spacing: -0.02em;
    text-transform: uppercase;
    color: #000000;
}



.forColorcheckbox input[type="radio"] {
    display: none;
    width: auto;
}

.forColorcheckbox label {
    width: 100%;
    flex: 0 0 21%;
    flex-wrap: wrap;
}

.forColorcheckbox input[type="radio"]:checked + span {
    font-size: 18px;
    position: relative;
}
.forColorcheckbox input[type=radio]:checked + span::before {
    content: '';
    background-color: #d5001c;
    width: 20px;
    height: 20px;
    position: absolute;
    right: 5px;
    bottom: 5px;
    border: 1px solid #fff;
    border-radius: 20px;
    z-index: 1;
}
span.tick {
    position: absolute;
    z-index: 2;
    right: 9px;
    bottom: 4px;
    font-size: 14px;
}

span.tick i {
    color: #fff;
    font-size: 13px;
    transform: rotate(354deg);
    font-weight: 600;
    font-family: 'Font Awesome 6 Free';
}


.forColorcheckbox input[type="radio"] + span {
    width: 100%;
    height: 85px;
    background: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0;
    margin: 0 !important;
} 

.form-group.forColorcheckbox {
    padding: 30px 0px;
