div .modal-dialog {
    width: 80%;
    max-width: 80%;
    background: transparent;
    background-color: transparent;
}
div .modal-content {
    background-color: transparent;
    border: none
}

.modalCustom h3 {
    font-size: 18px;
    line-height: 26px;
    margin: 10px 0;
    font-weight: 600;
}

.modalCustom h2.main-text {
    font-size: 32px;
    margin-bottom: 24px;
}

.modalCustom p {
    font-size: 14px;
    padding-right: 20px;
}

.modalCustom a {
    padding: 11px;
    background: transparent;
    color: #000;
    margin-right: 10px;
    min-width: 140px;
    display: inline-block;
    text-align: center;
    outline: none;
    -webkit-outline: none;
    border: 1px solid #000;
    font-size: 14px;
    transition: 0.5s ease all;
}

.modalCustom a:hover {
    background: #ff7d00;
    color: #fff;
    border-color: #fff;
}

.modalCustom .carousel .control-dots .dot {
    width: 12px;
    height: 12px;
    background: #ff7d00;
    box-shadow: 0 0 1px 2px rgb(255 255 255 / 90%);
}
.modalCustom .carousel.carousel-slider .slide img {
    width: 100%;
    vertical-align: top;
    border: 0;
    height: 600px;
    transition-duration: 0.2s;
    object-fit: cover;
    object-position: 20%;
}
.modal-dialog.edit-mode .modal-header button span{
    font-weight: 300;
}
